              shadowOffsetX: 0,
              shadowOffsetY: 0,
              opacity: 1,
              shadowBlur: 8,
              type: "solid",
              width: 3
            },
          },
        ]

        this.AddYaxis5 = {
          type: 'value',
          name: '单位：个',
          // interval: 4,
          max: () => {
            let dataList = [Math.max(...this.chartData5[0].data), Math.max(...this.chartData5[1].data), Math.max(...this.chartData5[2].data)];
            let max = Math.max(...dataList);
            for (let i = 0; i <= 200; i++) {
              if (i % 5 == 0) {
                if (i >= max) {
                  return i;
                }
              }
            }
          },
          nameTextStyle: {
            color: "#66B9FF",
          },
          axisLabel: { //设置y轴的字
            show: true,
            // formatter: '{value} ml',
            color: '#66B9FF',
            fontWeight: 'bold',
            interval: 0,//使y轴横坐标全部显示
            textStyle: {//y轴字体样式
              color: '#66B9FF',
              margin: 15,
            }
          },
          splitLine: {//网格线样式
            show: true,
            lineStyle: {
              color: '#66B9FF',
              width: 1,
              type: 'solid'
            }
          },
          axisTick: { //y轴刻度线
            show: false,
          },
          axisLine: { //y轴
            show: false,
          },
        }
      },

      // 选项改变
      dataChange(type, key, dataName, date) {
        window.console.log(type, key, dataName);
        if (key) {
          if (date) {
            this[dataName].forEach((item) => {
              item.data = this.listData[type].find(res => res.label === key).data[date][item.name];
            })
          } else {
            this[dataName].forEach((item) => {
              item.data = this.listData[type].find(res => res.label === key)[item.name];
            })
          }
        } else if (date) {
          if (type == 'rwcz') {
            this[dataName] = this.listData[type][date];
            return false;
          } else {
            this[dataName].forEach((item) => {
              item.data = this.listData[type][date][item.name];
            })
          }
        }


        this.$refs[type].init();
      }
    },
  }
